import React, { FC } from 'react'
import { Header } from './Header';
import { useNavigation } from '@react-navigation/native';
import { Screen, ScreenProps } from './Screen';
import { Dimensions, Platform, SafeAreaView, ScrollView, StyleProp, View, ViewProps, ViewStyle } from 'react-native';
import { HSBCBrandColor } from './HSBCComponents/Consant';
import { useSafeAreaInsets } from 'react-native-safe-area-context';


interface HexBaseScreenProps extends ViewProps {
  title?: String
  scrollEnabled?: boolean
  onScroll?: Function
  containerStyle?: ViewStyle
}

export const BaseScreen: FC<HexBaseScreenProps> = (props: HexBaseScreenProps) => {

  const {
    title,
    children,
    style,
    scrollEnabled=false,
    onScroll=() => {},
    containerStyle,
    ...rest
  } = props

  const windowHeight = Dimensions.get('window').height;
  const { 
    top, 
    bottom,
  } = useSafeAreaInsets();
  const navHeaderHeight = 56;

  const navigation = useNavigation()

  const containerViewStyle = () => {
    if (title) {
      return (
        {
          height: windowHeight - bottom - top - navHeaderHeight,
        }
      )
    } else {
      return (
        {
          height: windowHeight - bottom - top,
          marginTop: top,
        }
      )
    }
  }

  const renderBody = () => {
    if (scrollEnabled) {
      return (
        <ScrollView
          style={[
            containerViewStyle(),
            containerStyle,
          ]}
          onScroll={() => onScroll()}
        >
          {children}
        </ScrollView>
      )
    } else {
      return (
        <View
          style={[
            containerViewStyle(),
            containerStyle,
          ]}
        >
          {children}
        </View>
      )
    }
  }

  return (
    <Screen 
      style={[{
        flex: 1,
      }, style]}
      preset={'fixed'}
      {...rest}
    >
      {title && <Header
        backgroundColor='white'
        title={title as string}
        leftIcon="back"
        onLeftPress={() => {
          navigation.goBack();
        }}
      />}

      {
        title && 
        <View
          style={{
            backgroundColor: HSBCBrandColor.grey3,
            width: '100%',
            height: 0.5,
          }}
        />
      }

      {renderBody()}
      
    </Screen>
  )

}