export const HSBCButton = (props: HSBCButtonProps) => {
  const {
    theme='default',
    text,
    style,
    children,
    ...rest
  } = props

  function showContent(theme:string ,text:String) {
    if (['loading', 'darkLoading'].includes(theme)) {
      return (
        <ActivityIndicator color={'white'} />
      )
    } else {
      return (
        <HSBCText
          fontSize={HSBCFont.fontSize.S5}
          fontColor={HSBCButtonTheme[theme].fontColor}
          style={{
            textAlign: 'center',
          }}
        >
          {text}
        </HSBCText>
      )
    }
  }

  return (
    <TouchableOpacity
      style={[
        style,
        {
          height:48,
          justifyContent: 'center',
        },
        HSBCButtonTheme[theme],
      ]}
      disabled={theme==='disabled'}
      {...rest}
     >
      {showContent(theme, text)}
    </TouchableOpacity>
  )
}

export const HSBCButtonTheme = {
  default: {
    backgroundColor: HSBCBrandColor.red,
    fontColor: 'white',
  },
  white: {
    fontColor: 'black',
    borderWidth: 1,
    borderColor: 'black',
  },
  disabled: {
    backgroundColor: '#D1D3D0',
    fontColor: 'white',
  },
  loading: {
    backgroundColor: HSBCBrandColor.red,
    fontColor: 'white',
  },
  success: {
    backgroundColor: HSBCRagColor.success,
    fontColor: 'white',
  },
  dark: {
    backgroundColor: 'black',
    fontColor: 'white',
  },
  darkLoading: {
    backgroundColor: '#383838',
  },
}