import React, { FC, useState } from "react"
import { observer } from "mobx-react-lite"
import { ViewStyle } from "react-native"
import { AppStackScreenProps } from "app/navigators"
import { Button, Screen, Text } from "app/components"
import { useNavigation } from "@react-navigation/native"
import { useStores } from "app/models"
import * as WebBrowser from 'expo-web-browser';

interface SafariScreenProps extends AppStackScreenProps<"Safari"> {}

export const SafariScreen: FC<SafariScreenProps> = observer(function SafariScreen() {
  // Pull in one of our MST stores
  // const { someStore, anotherStore } = useStores()

  // Pull in navigation via hook
  const navigation = useNavigation()

  const [result, setResult] = useState(null);

  const _handlePressButtonAsync = async () => {
    let result = await WebBrowser.openBrowserAsync('https://web.hex-dev.non-prod.welab.tech/');
    setResult(result);
  };

  return (
    <Screen style={$root} preset="scroll">
      <Text text="safari" />

      <Button
        text="go back"
        onPress={() => {
          navigation.goBack();
        }}
      />

      <Button
        text="In-app browser (Safari)"
        onPress={_handlePressButtonAsync}
      />
    </Screen>
  )
})

const $root: ViewStyle = {
  flex: 1,
}
