import React, { FC, forwardRef, useImperativeHandle, useRef, useState } from 'react'
import { Animated, StyleProp, TextInput, TextStyle, TouchableWithoutFeedback, View, ViewProps, ViewStyle } from "react-native"
import { colors, typography } from "app/theme"
import { Text } from "app/components/Text"
import { HSBCText } from "./HSBCText"
import { HSBCBrandColor, HSBCFont, HSBCRagColor } from "./Consant"
import { delay } from "app/utils/delay"

interface HSBCTextInput2Props extends ViewProps {
  /**
   * An optional style override useful for padding & margin.
   */
  // style?: StyleProp<ViewStyle>
  ref?
}

/**
 * Describe your component here
 */
export const HSBCTextInput2: FC<HSBCTextInput2Props> = forwardRef((props:HSBCTextInput2Props, ref) => {
  const { 
    style,
    ...rest
  } = props
  // const $styles = [$container, style]

  const inputRef = useRef(null)
  // title24
  const fontSize = useRef(new Animated.Value(24)).current;

  const [showInput, setShowInput] = useState(false)

  useImperativeHandle(ref, () => ({
    blur: () => {
      inputRef.current.blur()
    }
  }))

  const fontSizeAnim = (toValue = 14, callback?) => {
    Animated.timing(fontSize, {
      toValue,
      duration: 300,
      useNativeDriver: false,
    }).start(callback); 
  };

  return (
    <TouchableWithoutFeedback
      onPress={() => {
        setShowInput(true)
        
        fontSizeAnim(14, () => {
          inputRef.current.focus()
        })
      }}
      {...rest}
    >
      <View
        style={{
          borderWidth: 1,
          borderColor: 'black',
          // borderRadius: 8,
          paddingHorizontal: 16,
          justifyContent: 'center',
          // minHeight: 36,
          height: 72,
        }}
      >
        <Animated.Text
          style={{
            fontSize,
            fontWeight: '200',
            // height: 24,
          }}
        >
          title
        </Animated.Text>

        {
          <TextInput
            placeholder="asd"
            clearButtonMode="while-editing"
            style={{
              // height: 24,
              marginVertical: 4,
              display: showInput ? 'flex' : 'none',
            }}
            ref={inputRef}
            onFocus={() => {
            }}
            onBlur={() => {
              console.log(inputRef.current.value);
              
              if (!inputRef.current.value) {
                fontSizeAnim(24)
                setShowInput(false)
              }
            }}
          >
          </TextInput>
        }
      </View>
    </TouchableWithoutFeedback>
  )
})


export interface HSBCTextInputProps extends ViewProps {
  ref?
  styleType?: 'small' | 'large'
  status?: 'default' | 'active' | 'disabled' | 'error' | string
  placeholder?: string | undefined
  defaultValue?: string | undefined
  /// 00
  countLimit?: number
}

export const HSBCTextInput: FC<HSBCTextInputProps> = forwardRef((props:HSBCTextInputProps, ref) => {
  const {
    style,
    styleType = 'small',
    status,
    placeholder,
    defaultValue,
    countLimit = 0,
    ...rest
  } = props

  const [focus, setFocus] = useState(status === 'active')
  const [contentText, setContentText] = useState(defaultValue ?? '')

  const inputRef = useRef(null)

  useImperativeHandle(ref, () => ({
    blur: () => {
      inputRef.current.blur()
    }
  }))

  return (
    <View>
      <TextInput
        placeholder={placeholder}
        clearButtonMode="while-editing"
        defaultValue={defaultValue}
        style={[
          {
            borderColor: 'black',
            borderWidth: 1,
            height: 48,
            fontSize: HSBCFont.fontSize.S5,
            fontWeight: '600',
            paddingHorizontal: 10,
            color: HSBCBrandColor.grey33,
            borderBottomWidth: focus ? 3 : 1,
          },
          styleType === 'large' ? $largeStyle : {},
          status === 'error' ? $errorStyle : {},
          status === 'disabled' ? $disabledStyle : {},
          style,
        ]}
        ref={inputRef}
        autoFocus={focus}
        editable={status !== 'disabled'}
        onFocus={() => {
          setFocus(true)
        }}
        onBlur={() => {
          setFocus(false)
        }}
        onChangeText={text => {
          setContentText(text)
        }}
        maxLength={countLimit>0 ? countLimit : 999999999}
        {...rest}
      />

      {
        countLimit > 0 && (
          <HSBCText
            fontSize={HSBCFont.fontSize.S7}
            style={{
              alignSelf: 'flex-end',
              marginTop: 2,
            }}
          >
            {contentText.length}/{countLimit}
          </HSBCText>
        )
      }
    </View>
  )
})

const $largeStyle: TextStyle = {
  height: 62,
  fontSize: HSBCFont.fontSize.S3,
}

const $disabledStyle: ViewStyle = {
  borderColor: HSBCBrandColor.greyD7,
  backgroundColor: 'white',
}

const $errorStyle: ViewStyle = {
  borderColor: HSBCRagColor.error,
  backgroundColor: '#F9F2F3',
}