import * as React from "react"
import { ColorValue, StyleProp, Text, TextProps, TextStyle, View, ViewStyle } from "react-native"
import { observer } from "mobx-react-lite"
import { colors, typography } from "app/theme"
// import { Text, TextProps } from "app/components/Text"
import { HSBCFont, HSBCBrandColor } from './Consant'

// export const HSBCTextColor = {
//   normal: '#333333',

// }

interface HSBCTextProps extends TextProps {
  /**
   * An optional style override useful for padding & margin.
   */
  // style?: StyleProp<TextStyle>
  style?: StyleProp<TextStyle>
  fontColor?: ColorValue
  fontSize?: number | undefined;
  // fontColor?: HSBCThemeColo

  children?: React.ReactNode;
}

// export const BaseScreen: FC<HexBaseScreenProps> = (props: HexBaseScreenProps) => {
export const HSBCText: React.FC<HSBCTextProps> = (props: HSBCTextProps) => {

  const {
    children,
    fontColor = HSBCBrandColor.defaulText,
    fontSize = HSBCFont.fontSize.S5,
    style,
    ...rest
  } = props

  return (
    <Text
      // weight="bold"
      style={[
        {
          color: fontColor,
          fontSize: fontSize,
          lineHeight: fontSize + 6,
          fontWeight: '400',
        },
        style,
      ]}
      // onPress={() => {}}
      {...rest} 
      >
      {children}
    </Text>
  )
}

